import Link from 'next/link';
import Image from 'next/image';

export default function Footer() {
  const currentYear = new Date().getFullYear();

  return (
    <footer className="bg-kensu-light-gray">
      <div className="container mx-auto px-6 py-10">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
          {/* Company Info */}
          <div>
            <Image
              src="/assets/images/logo/kensu-logo.png"
              alt="Kensu Logo"
              width={120}
              height={72}
              className="mb-4"
            />
            <p className="text-gray-600 text-sm">
              Organiza la vida familiar de manera sencilla y eficiente.
            </p>
          </div>

          {/* Quick Links */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-4">Enlaces Rápidos</h4>
            <ul className="space-y-2 text-sm">
              <li>
                <Link href="#features" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Características
                </Link>
              </li>
              <li>
                <Link href="#how-it-works" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Cómo Funciona
                </Link>
              </li>
              <li>
                <Link href="#benefits" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Beneficios
                </Link>
              </li>
            </ul>
          </div>

          {/* Support */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-4">Soporte</h4>
            <ul className="space-y-2 text-sm">
              <li>
                <Link href="#" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Centro de Ayuda
                </Link>
              </li>
              <li>
                <Link href="#" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Contacto
                </Link>
              </li>
              <li>
                <Link href="#" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Términos y Condiciones
                </Link>
              </li>
              <li>
                <Link href="#" className="text-gray-600 hover:text-kensu-green transition-colors">
                  Política de Privacidad
                </Link>
              </li>
            </ul>
          </div>

          {/* Social Media */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-4">Síguenos</h4>
            <div className="flex space-x-4">
              <a
                href="#"
                className="text-gray-600 hover:text-kensu-green transition-colors"
                aria-label="Facebook"
              >
                <i className="fab fa-facebook text-2xl"></i>
              </a>
              <a
                href="#"
                className="text-gray-600 hover:text-kensu-green transition-colors"
                aria-label="Instagram"
              >
                <i className="fab fa-instagram text-2xl"></i>
              </a>
              <a
                href="#"
                className="text-gray-600 hover:text-kensu-green transition-colors"
                aria-label="Twitter"
              >
                <i className="fab fa-twitter text-2xl"></i>
              </a>
            </div>
          </div>
        </div>

        {/* Copyright */}
        <div className="border-t border-gray-300 mt-8 pt-6 text-center">
          <p className="text-gray-600 text-sm">
            © {currentYear} Kensu. Todos los derechos reservados.
          </p>
        </div>
      </div>
    </footer>
  );
}
