export default function FeaturesSection() {
  const features = [
    {
      icon: 'fa-calendar-check',
      title: 'Calendario Compartido',
      description: 'Organiza eventos familiares, escolares y personales en un solo calendario sincronizado.',
      color: 'text-kensu-green',
    },
    {
      icon: 'fa-users',
      title: 'Gestión Familiar',
      description: 'Administra a todos los miembros de tu familia y asigna tareas fácilmente.',
      color: 'text-kensu-orange',
    },
    {
      icon: 'fa-bell',
      title: 'Recordatorios Automáticos',
      description: 'Recibe notificaciones inteligentes de eventos próximos y tareas pendientes.',
      color: 'text-kensu-green',
    },
    {
      icon: 'fa-cloud',
      title: 'Sincronización en la Nube',
      description: 'Accede a tu información desde cualquier dispositivo, siempre actualizada.',
      color: 'text-kensu-orange',
    },
  ];

  return (
    <section id="features" className="py-20 bg-kensu-light-gray">
      <div className="container mx-auto px-6 text-center">
        <h2 className="text-4xl font-bold text-gray-800 mb-4">
          Características Principales
        </h2>
        <p className="text-xl text-gray-600 mb-16 max-w-2xl mx-auto">
          Todo lo que necesitas para organizar tu vida familiar en un solo lugar
        </p>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {features.map((feature, index) => (
            <div
              key={index}
              className="bg-white p-8 rounded-2xl shadow-md hover:shadow-xl transition-all hover:-translate-y-2 duration-300"
            >
              <div className={`${feature.color} text-5xl mb-4`}>
                <i className={`fas ${feature.icon}`}></i>
              </div>
              <h3 className="text-xl font-semibold text-gray-800 mb-2">
                {feature.title}
              </h3>
              <p className="text-gray-600">{feature.description}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
