import Link from 'next/link';

export default function CTASection() {
  return (
    <section id="cta" className="bg-gradient-to-r from-kensu-green to-kensu-orange">
      <div className="container mx-auto px-6 py-20 text-center">
        <h2 className="text-4xl md:text-5xl font-bold text-white mb-6">
          Comienza a Organizar tu Vida Familiar Hoy
        </h2>
        <p className="text-xl text-white/90 mb-8 max-w-2xl mx-auto">
          Descarga Kensu y descubre lo fácil que puede ser mantener a tu familia conectada y organizada.
        </p>

        <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
          <Link
            href="#download"
            className="bg-white text-kensu-green px-8 py-4 rounded-full hover:shadow-2xl transition-all font-semibold text-lg flex items-center gap-2"
          >
            <i className="fab fa-google-play"></i>
            Google Play
          </Link>
          <Link
            href="#download"
            className="bg-white text-kensu-orange px-8 py-4 rounded-full hover:shadow-2xl transition-all font-semibold text-lg flex items-center gap-2"
          >
            <i className="fab fa-apple"></i>
            App Store
          </Link>
        </div>

        <p className="text-white/80 mt-6">
          ¡Completamente gratis!
        </p>
      </div>
    </section>
  );
}
