export default function HowItWorksSection() {
  const steps = [
    {
      number: '1',
      icon: 'fa-download',
      title: 'Descarga la aplicación',
      description: 'Disponible gratis en App Store y Google Play.',
    },
    {
      number: '2',
      icon: 'fa-user-plus',
      title: 'Crea tu perfil familiar',
      description: 'Configura tu familia y añade a todos los miembros.',
    },
    {
      number: '3',
      icon: 'fa-calendar-plus',
      title: 'Comienza a planificar',
      description: 'Crea eventos, asigna tareas y establece recordatorios.',
    },
    {
      number: '4',
      icon: 'fa-check-circle',
      title: 'Disfruta de la organización',
      description: 'Mantén a toda la familia sincronizada y feliz.',
    },
  ];

  return (
    <section id="how-it-works" className="py-20 bg-white">
      <div className="container mx-auto px-6">
        <h2 className="text-4xl font-bold text-center text-gray-800 mb-4">
          ¿Cómo Funciona?
        </h2>
        <p className="text-xl text-gray-600 text-center mb-16 max-w-2xl mx-auto">
          Comienza a organizar tu vida familiar en 4 simples pasos
        </p>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {steps.map((step, index) => (
            <div key={index} className="text-center relative">
              {/* Connector Line (except for last item) */}
              {index < steps.length - 1 && (
                <div className="hidden lg:block absolute top-16 left-1/2 w-full h-0.5 bg-gradient-to-r from-kensu-green to-kensu-orange"></div>
              )}

              {/* Step Number Circle */}
              <div className="relative z-10 w-32 h-32 mx-auto mb-6 bg-gradient-to-br from-kensu-green to-kensu-orange rounded-full flex items-center justify-center shadow-lg">
                <span className="text-5xl font-bold text-white">{step.number}</span>
              </div>

              {/* Icon */}
              <div className="text-kensu-green text-4xl mb-4">
                <i className={`fas ${step.icon}`}></i>
              </div>

              {/* Title */}
              <h3 className="text-xl font-semibold text-gray-800 mb-2">
                {step.title}
              </h3>

              {/* Description */}
              <p className="text-gray-600">{step.description}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
