import Image from 'next/image';

export default function BenefitsSection() {
  const benefits = [
    {
      title: 'Ahorra Tiempo',
      description: 'Centraliza toda la información familiar en un solo lugar y reduce el tiempo de coordinación.',
      icon: 'fa-clock',
    },
    {
      title: 'Mejora la Convivencia',
      description: 'Evita malentendidos y conflictos con una comunicación clara y organizada.',
      icon: 'fa-heart',
    },
    {
      title: 'Reduce el Estrés',
      description: 'Olvídate de las preocupaciones con recordatorios automáticos y notificaciones oportunas.',
      icon: 'fa-smile',
    },
    {
      title: 'Privacidad Garantizada',
      description: 'Tu información familiar está protegida con los más altos estándares de seguridad.',
      icon: 'fa-lock',
    },
  ];

  return (
    <section id="benefits" className="py-20 bg-gradient-to-b from-kensu-green/10 to-white">
      <div className="container mx-auto px-6">
        <h2 className="text-4xl font-bold text-center text-gray-800 mb-4">
          Beneficios de Kensu
        </h2>
        <p className="text-xl text-gray-600 text-center mb-16 max-w-2xl mx-auto">
          Descubre cómo Kensu puede mejorar la organización de tu familia
        </p>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          {/* Left - Benefits List */}
          <div className="space-y-6">
            {benefits.map((benefit, index) => (
              <div
                key={index}
                className="flex items-start gap-4 bg-white p-6 rounded-xl shadow-md hover:shadow-lg transition-shadow"
              >
                <div className="flex-shrink-0 w-12 h-12 bg-gradient-to-br from-kensu-green to-kensu-orange rounded-full flex items-center justify-center text-white">
                  <i className={`fas ${benefit.icon} text-xl`}></i>
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-gray-800 mb-2">
                    {benefit.title}
                  </h3>
                  <p className="text-gray-600">{benefit.description}</p>
                </div>
              </div>
            ))}
          </div>

          {/* Right - Image/Illustration */}
          <div className="flex justify-center">
            <div className="relative w-full max-w-lg">
              <Image
                src="/assets/images/screenshots/app-benefits.png"
                alt="Kensu App Benefits"
                width={500}
                height={600}
                className="rounded-2xl shadow-2xl"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
